 

 body, html {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Roboto Slab', serif; 
  line-height: 1.8;
  background-color:white;
 }

 
img {
  width: 100%;
  object-fit: contain;
  opacity: 0.3;
}
 
.introduction{
  background: linear-gradient(to left top, #43cea2 , #185a9d);
  overflow: hidden;
  height: 100%;
  z-index: 2;
  opacity: 0.85;
}
.textwrapper {
  width: 100%;
  position: relative;
  margin-top: -20%;
  text-align: center;
  color: wheat;
}
 ul {
   margin-bottom: 0;
 }
 h1, h2, h3, h4, p{
  margin: 0;
  padding: 0;
}
.mystyle {
  padding: 15px;
}

.introduction { 
  width: 50%;
  height: 100%;
  float: left;
  overflow: hidden;
  position: fixed;
}
.test {
  display: block;
  width: 50%;
  height: 100%;
  float: right;
 
}

.intro, .summary, .technologies, .skills, .experience, .education {
  display: grid;
  grid-template-columns: 1fr 4fr;
}


.mystyleflex {
  display: flex;
}
.thedate {
  margin-left: auto;
}
.myintro, .mysummary,.myskills, .myexperience, .myeducation, .mytarget {
  color: rgb(35, 107, 189);
  font-weight: 800;
}
.mytarget {
  margin: 0;
  padding: 0;
}

 
@media (max-width: 480px) {
  
  body, html {
    padding: 0;
    margin: 0;
  }
  h1 {
    
    font-size: 23px;
  }
  .container {
     display: flex;
     flex-direction: column;
    
  }

  .introduction {
    width: 100%;
    position: relative;
    border: 1px solid red;
    height: 100vh;
    margin-bottom: 15px;
  }
  img {
    height: 100%;
    object-fit: cover;
  }

  .textwrapper {
    margin-top: -40%;
  }

  .textwrapper >h2 {
    font-size: 30px;
    opacity: 0.7;
  }
  .test {
    width: 100%;
    position: relative;
   
  }

  .intro, .summary, .technologies, .experience, .education {
    display: flex;
    flex-direction: column;
  }
  .myintro {
    font-size: 12px;
  }
  .mystyle  {
    padding-top: 0;
    padding-bottom: 15px;
  }
}

@media only screen and (min-width: 481px) and (max-width: 979px) {

  Body, html {
   margin: 0;
   padding: 0;
   
  }
  .container {
    display: flex;
    flex-direction: column;
   
 }
 .introduction {
  width: 100%;
  position: relative;
  border: 1px solid red;
  height: 100vh;
  margin-bottom: 15px;
  }
  img {
    height: 100%;
    object-fit: cover;
  }
  .textwrapper {
    margin-top: -35%;
  }

  .textwrapper >h2 {
    font-size: 30px;
    opacity: 0.7;
  }
  .test {
    width: 100%;
    position: relative;
   
  }
  .textwrapper >h2 {
    font-size: 35px;
    opacity: 0.7;
  }

  .mytarget {
    font-size: 22px;
  }
}
@media only screen and (min-width: 978px){

  img {
    height: 100%;
    object-fit: cover;
  }

}
